//
// Variables
// --------------------------------------------------

// Fonts
// -------------------

$headingFont: 'Roboto', sans-serif;
$paragraphFont: 'Roboto', sans-serif;

// Colors
$blue: blue;


// Component Sections (can be overriden)
$adminBarBackground: #EFEFEF;

// Size:
$space: 2em;

// Vars
// -------------------

$primaryColor: $blue;
$secondaryColor: #9d6ab4;

$textColor: #000000;
$baseColor: #EEEEEE;


$errorColor: #AA0000;
$successColor: #00AA00;
$baseFontSize: 16;

// Breakpoints
// -------------------

$breakpoints: (
	'mobile': (max-width: 736px),
	'tablet': (max-width: 1024px),
	'desktop': (min-width: 1200px),
);

// Include gutter on outside
$gutterOnOutside: true;

// Breakpoints Grid Settings
// -------------------

$grid-settings: (
	base: (
		container-columns: 12,
		gutter: 1%,
		max-width: 1100px,
	),
	desktop: (
		container-columns: 12,
		gutter: 1%,
		max-width: 1200px,
	),
	tablet: (
		container-columns: 12,
		gutter: 5%,
	),
	mobile: (
		container-columns: 1,
		gutter: 5%,
	)
);
